
export default {
  props: ['modalActive', 'coin'],
  data() {
    return {
      description: '',
      sanitizedHtml: '',
    };
  },
  created() {
    // Sanitize the HTML content using DOMPurify
    this.sanitizedHtml = DOMPurify.sanitize(this.description);
  },
  setup(props, { emit }) {
    const close = () => {
      emit('close');
    };
    // console.log(props);

    return { close };
  },
};
